<template>
  <div>
    <Header name="会员等级"></Header>
    <div>
      <!-- style="min-height: 812px; background: #FAFAFA;" -->
      <div class="step-gray"></div>
      <div class="step-gray-sel" :style="{height: selHeight}"></div>
      <div class="steps-dt steps1">
        <div class="star">三星</div>
        <img :src="stepsRed > 1 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          50万<span>积分</span>
          <div>-</div>
        </div>
      </div>
      <div class="steps-dt steps2">
        <div class="star">四星</div>
        <img :src="stepsRed > 2 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          100万<span>积分</span>
          <div>-</div>
        </div>
      </div>
      <div class="steps-dt steps3">
        <div class="star">五星</div>
        <img :src="stepsRed > 3 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          200万<span>积分</span>
          <div>返2%</div>
        </div>
      </div>
      <div class="steps-dt steps4">
        <div class="star">六星</div>
        <img :src="stepsRed > 4 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          500万<span>积分</span>
          <div>返3%</div>
        </div>
      </div>
      <div class="steps-dt steps5">
        <div class="star">七星</div>
        <img :src="stepsRed > 5 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          1000万<span>积分</span>
          <div>返5%</div>
        </div>
      </div>
      <div class="steps-dt steps6">
        <div class="star">八星</div>
        <img :src="stepsRed > 6 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          2000万<span>积分</span>
          <div>返8%</div>
        </div>
      </div>
      <div class="steps-dt steps7">
        <div class="star">九星</div>
        <img :src="stepsRed > 7 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          5000万<span>积分</span>
          <div>返10%</div>
        </div>
      </div>
      <div class="steps-dt steps8">
        <div class="star">十星</div>
        <img :src="stepsRed > 8 ? require('@/assets/image/spot_m.png') : require('@/assets/image/spot_gray_m.png')">
        <div class="star-end">
          10000万<span>积分</span>
          <div>返10%</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'

export default {
  name: 'home',
  components :{
    Header
  },
  data () {
    return {
      stepsRed: 1,
      selHeight: '1%',
      hdlForm: {
        pageNum: 1,
        pageSize: 5,
        withDetail: false
      }
    }
  },
  mounted () {
    this.handleGrade()
  },
  methods: {
    handleGrade () {
      let self = this
      api.memberIntegration(self.hdlForm).then(res => {
        if (res.code === 200) {
          self.chgeProportion(res.data.current)
        } else {
          self.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
          self.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
      }).finally(() => {
      })
    },
    chgeProportion (num) {
      let self = this
      num = num > 0 ? num : 0
      // num = 100000000000
      if (num <= 500000) {
        self.stepsRed = num === 500000 || 500000 < num ? 1.5 : 1
        self.selHeight = num/500000*5
      } else if (num <= 1000000) {
        self.stepsRed =  num === 1000000 || 1000000 < num ? 2.5 : 2
        self.selHeight = 7 + (num-500000)/500000*8
      } else if (num <= 2000000) {
        self.stepsRed = num === 2000000 || 2000000 < num ? 3.5 : 3
        self.selHeight = 17 + (num-1000000)/1000000*8
      } else if (num <= 5000000) {
        self.stepsRed = num === 5000000 || 5000000 < num ? 4.5 : 4
        self.selHeight = 27 + (num-2000000)/3000000*8
      } else if (num <= 10000000) {
        self.stepsRed = num === 10000000 || 10000000 < num ? 5.5 : 5
        self.selHeight = 37 + (num-5000000)/5000000*8
      } else if (num <= 20000000) {
        self.stepsRed = num === 20000000 || 20000000 < num ? 6.5 : 6
        self.selHeight = 47 + (num-10000000)/10000000*8
      } else if (num <= 50000000) {
        self.stepsRed = num === 50000000 || 50000000 < num ? 7.5 : 7
        self.selHeight = 57 + (num-20000000)/30000000*8
      } else if (num <= 100000000) {
        self.stepsRed = num === 100000000 || 100000000 < num ? 8.5 : 8
        self.selHeight = 67 + (num-50000000)/50000000*8
      } else if (num > 100000000){
        self.stepsRed = 8.5
        self.selHeight = 77 + ((num-100000000)/100000000*6 >= 6 ? 6 : (num-100000000)/100000000*6)
      } else {
        self.stepsRed = 1
        self.selHeight = 1
      }
      self.selHeight = self.selHeight + '%'
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '~@/styles/variable.styl';
.steps-dt {
  position: fixed;
  left: 19%;
  z-index: 999;
}
.star {
  position: fixed;
  left: 55px;
  color: #000000;
  font-size: 16px;
  font-weight: bold;
}
.star-end {
  position: fixed;
  left: 130px;
  color: #000000;
  font-size: 16px;
  font-weight: bold;
  span {
    font-size: 14px;
  }
  div {
    color: #999999;
    font-size: 14px;
    margin-top: 5px;
    font-weight: bold;
  }
}
.steps1 {
  top: 15%;
}
.steps2 {
  top: 25%;
}
.steps3 {
  top: 35%;
}
.steps4 {
  top: 45%;
}
.steps5 {
  top: 55%;
}
.steps6 {
  top: 65%;
}
.steps7 {
  top: 75%;
}
.steps8 {
  top: 85%;
}
img {
  z-index: 999;
  width: 15px;
  position: fixed;
  left: 100px;
  z-index: 999;
}
.step-gray {
  position: fixed;
  top: 10%;
  left: 104px;
  height: 83%;
  width: 2px;
  border-right: 3px solid #D8D8D8;
  z-index: 88;
}
.step-gray-sel {
  position: fixed;
  top: 10%;
  left: 104px;
  /* height: 17%; */
  width: 2px;
  border-right: 3px solid #FF4B4B;
  z-index: 99;
}
</style>



